/** Media Queries **/
.informacion {
  margin-top: 2rem;
  margin-bottom: 2rem;
}
.informacion legend {
  color: #2c2c51;
  font-size: 2rem;
  font-weight: bold;
}
.informacion .icon {
  width: 40px;
}
.informacion h4 {
  display: flex;
  flex-flow: row;
  align-items: center;
}

.division {
  display: flex;
  flex-flow: column;
}
@media (min-width: 768px) {
  .division {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 2rem;
  }
}

.boton-cancelar {
  display: block;
  background-color: green;
  padding: 1.5rem;
  border-radius: 1rem;
  color: aliceblue;
  font-weight: 900;
  font-size: 2rem;
  border: none;
}

.elementos-estado-riego {
  display: flex;
  flex-direction: column;
  margin-left: 2rem;
}
.elementos-estado-riego input {
  width: max-content;
  margin: auto;
}

.encabezado h2 {
  display: flex;
  flex-flow: row;
  align-items: center;
  justify-content: center;
}

.menu-horizontal {
  list-style-type: none;
  padding: 0;
  display: flex;
  flex-flow: row;
  justify-content: space-around;
  background-color: rgba(0, 0, 0, 0.5);
  align-items: center;
}
.menu-horizontal a {
  font-size: 2rem;
}

.menu-horizontal > li > a {
  display: flex;
  padding: 15px 20px;
  color: white;
  text-decoration: none;
}

.menu-horizontal > li:hover {
  background-color: red;
}

.menu-vertical {
  position: absolute;
  display: none;
  list-style-type: none;
  padding: 0;
  width: 200px;
  background-color: rgba(0, 0, 0, 0.5);
}

.menu-horizontal li:hover .menu-vertical {
  display: block;
}

.menu-vertical li:hover {
  background-color: black;
}

.menu-vertical li a {
  display: block;
  color: white;
  text-decoration: none;
  padding: 15px 15px 15px 20px;
}

.div-configuracion {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  margin-top: 2rem;
  margin-bottom: 2rem;
}
.div-configuracion p {
  text-align: center;
  font-family: "Poppins", sans-serif;
  font-weight: bold;
}
.div-configuracion input {
  text-align: center;
  font-size: 2rem;
  width: fit-content;
}

.boton-guardar {
  display: flex;
  width: fit-content;
  background-color: #03DFF8;
  padding: 1.5rem 4rem;
  border: none;
  font-size: 2rem;
  font-weight: bold;
  color: black;
  margin-top: 6rem;
}
.boton-guardar:hover {
  cursor: pointer;
  background-color: #0CA6DF;
}

.div-reportes {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 2rem;
  margin-top: 2rem;
  text-align: center;
}
.div-reportes .caja-riego {
  background-color: #EC9B22;
  border-radius: 12px;
}
.div-reportes .caja-humedadSuelo {
  background-color: #03C74E;
  border-radius: 12px;
}
.div-reportes .caja-temperatura {
  background-color: #23B8FA;
  border-radius: 12px;
}
.div-reportes .caja-humedadRelativa {
  background-color: #0FB7EF;
  border-radius: 12px;
}
.div-reportes .icon {
  width: 150px;
  height: auto;
}
.div-reportes p {
  font-size: 2rem;
  font-weight: bold;
}

.elementos-caja-superior {
  display: flex;
  flex-flow: row;
  justify-content: center;
  align-items: center;
  padding: 1rem;
}

.elementos-caja-inferior {
  font-weight: bolder;
  color: black;
  font-size: 2.5rem;
  margin-top: 0;
}
.elementos-caja-inferior h4 {
  margin: 0;
}

.container-reportes {
  margin-bottom: 2rem;
}

.logo {
  width: 200px;
  height: auto;
  margin-bottom: 2rem;
}

.encabezado-login h2 {
  display: flex;
  flex-flow: row;
  align-items: center;
  justify-content: center;
  margin: 0;
  margin-top: 10rem;
  font-size: 5rem;
}

.fondo-login {
  background: linear-gradient(to bottom right, #2F5BFE, pink);
  font-family: sans-serif;
  height: 85vh;
}

.login-box {
  width: 350px;
  height: fit-content;
  background: #000;
  color: #fff;
  top: 50%;
  left: 50%;
  position: absolute;
  transform: translate(-50%, -50%);
  box-sizing: border-box;
  padding: 70px 30px;
}
.login-box h1 {
  color: #fff;
}

.login-box .avatar {
  width: 100px;
  height: 100px;
  border-radius: 50%;
  position: absolute;
  top: -50px;
  left: calc(50% - 50px);
}

.login-box h1 {
  margin: 0;
  padding: 0 0 20px;
  text-align: center;
  font-size: 22px;
}

.login-box label {
  margin: 0;
  padding: 0;
  font-weight: bold;
  display: block;
}

.login-box input {
  width: 100%;
  margin-bottom: 20px;
}

.login-box input[type=text], .login-box input[type=password] {
  border: none;
  border-bottom: 1px solid #fff;
  background: transparent;
  outline: none;
  height: 40px;
  color: #fff;
  font-size: 16px;
}

.login-box input[type=submit] {
  border: none;
  outline: none;
  height: 40px;
  background: #b80f22;
  color: #fff;
  font-size: 18px;
  border-radius: 20px;
}

.login-box input[type=submit]:hover {
  cursor: pointer;
  background: #ffc107;
  color: #000;
}

.login-box a {
  text-decoration: none;
  font-size: 12px;
  line-height: 20px;
  color: darkgrey;
}

.login-box a:hover {
  color: #fff;
}

.alerta {
  font-family: sans-serif;
  padding: 1.25rem;
  color: darkgrey;
  width: 100%;
  display: block;
  margin-bottom: 2rem;
  text-align: center;
  font-weight: 700;
  text-transform: uppercase;
}
.alerta.error {
  background-color: #b80f22;
}
.alerta.exito {
  background-color: #329f00;
}

* {
  margin: 0;
  padding: 0;
  transition: 0.5s;
}

footer {
  height: fit-content;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  background-color: #121212;
  color: white;
}

.copyright-container {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  border-top: 1px solid rgba(255, 255, 255, 0.3);
}

.copyright {
  font-size: 12px;
  opacity: 0.7;
  font-weight: 400;
  padding: 10px 0;
}

.footer-menus {
  display: flex;
  justify-content: center;
  align-items: flex-start;
  width: 40%;
}

.footer-container {
  height: fit-content;
  width: 100%;
  padding: 3rem 6rem;
  box-sizing: border-box;
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
}

.footer-content-container {
  display: flex;
  justify-content: flex-start;
  align-items: flex-start;
  flex-direction: column;
}

.footer .website-logo {
  margin-bottom: 1.2rem;
  font-size: calc(1vw + 20px);
}

.footer-info, .menu-item-footer {
  margin: 0.2rem 0;
  opacity: 0.7;
  color: white;
  text-decoration: none;
  transition: 0.5s;
}

.menu-item-footer:hover {
  opacity: 1;
}

.menu-title {
  font-size: 16px;
  font-weight: 400;
  text-transform: uppercase;
  margin-bottom: 1.2rem;
}

.social-container {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 30px;
  width: 100%;
}

.social-link {
  height: 100%;
  width: 30px;
  background-image: url("../img/facebook.png");
  background-size: 70%;
  background-position: center;
  margin-right: 1rem;
  background-repeat: no-repeat;
  width: 65px;
  height: 65px;
}

.social-link:hover {
  opacity: 0.7;
}

footer .social-link:nth-of-type(2) {
  background-image: url("../img/whatsapp.png");
}

footer .social-link:nth-of-type(3) {
  background-image: url(LinkedinBlanco.png);
}

@media (max-width: 768px) {
  .footer-container {
    padding: 2rem;
    flex-direction: column;
  }
  .footer-content-container {
    width: 100%;
    padding: 2rem 0;
    border-bottom: 1px solid #2a2a2a;
    justify-content: center;
    align-items: center;
  }
  .footer-content-container:nth-last-of-type(1) {
    align-items: flex-start;
  }
  .footer-content-container:nth-of-type(3) {
    border-bottom: none;
    padding-bottom: 0;
  }
  .footer-menus {
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    border-bottom: 1px solid #2a2a2a;
  }
  .footer-menus .footer-content-container:nth-of-type(2) {
    width: 45%;
    align-items: flex-start;
    border-bottom: none;
  }
}
html {
  height: 100%;
}

body {
  display: flex;
  flex-direction: column;
  min-height: 100%;
}

footer {
  margin-top: auto;
}