.div-reportes{
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 2rem;
    margin-top: 2rem;
    text-align: center;

    .caja-riego{
        background-color: #EC9B22;
        border-radius: 12px;
    }

    .caja-humedadSuelo{
        background-color: #03C74E;
        border-radius: 12px;
    }

    .caja-temperatura{
        background-color: #23B8FA;
        border-radius: 12px;
    }
    
    .caja-humedadRelativa{
        background-color: #0FB7EF;
        border-radius: 12px;
    }

    .icon{
        width: 150px;
        height: auto;
    }

    p{
        font-size: 2rem;
        font-weight: bold;
        
    }
}

.elementos-caja-superior{
    display: flex;
    flex-flow: row;
    justify-content: center;
    align-items: center; 
    padding: 1rem;
}

.elementos-caja-inferior{
    font-weight: bolder;
    color: black;
    font-size: 2.5rem;
    margin-top:  0;

    h4{
        margin: 0;
    }
}

.container-reportes{
    margin-bottom: 2rem;
}